body {
  background-image: linear-gradient(#59da3f, #1a9bbb);
  color: white;
  min-height: 100vh;
  }

h1 {
  display: flex;
  justify-content: center;
  font-size: 90px;
}

form {
  display: flex;
  justify-content: center;
  align-items: center;
}

form input {
  font-size: 30px;
  border-radius: 6px;
  border:none;
}

button {
  background-color: #4974c5;
  color: white;
  font-size: 21px;
  padding: 5px 20px;
  margin: 5px;
  border-radius: 6px;
  border: none;
}

button:hover {
  cursor: pointer;
  opacity: 0.8;
}

.todo-container {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  list-style:none;
  }

.completed {
text-decoration: line-through;
opacity: 0.5;
}
